<template>
    <b-row class="justify-content-md-center mt-4">
        <b-col md="7" class="tabs">
            <b-card no-body bg-variant="dark" text-variant="white">
                <b-tabs content-class="mt-3" pills card>
                    <b-tab title="ÁúãËøáÁöÑÁîµÂΩ±" active>
                        <b-card-group columns>
                            <b-card :title="item.title" v-for="(item,index) in movies" :key="index"
                                    bg-variant="dark" :id="`movie-card-${index}`" text-variant="white">
                                <b-card-text>
                                    <ul>
                                        <li>ËØÑÂàÜÔºö{{(new Array(item.star + 1)).join('‚≠ê')}}</li>
                                        <li>Ê†áËÆ∞Êó∂Èó¥Ôºö{{formatDate(new Date(item.date), 'yyyy-MM-dd')}}</li>
                                        <li>Áü≠ËØÑÔºö{{item.comment}}</li>
                                    </ul>
                                </b-card-text>
                                <b-popover :target="`movie-card-${index}`" triggers="click" @show="getDetail(item)"
                                           @hide="resetPopOver">
                                    <template v-slot:title>{{popoverItem.alt_title}}</template>
                                    <b-img thumbnail
                                           v-bind:src="popImageUrl"
                                           center/>
                                    <br/>
                                    <ul>
                                        <li>ËØÑÂàÜ: {{popoverItem.rating.average}}</li>
                                        <li v-for="(attr,name) in popoverItem.attrs" :key="name">
                                            {{name}}: {{(typeof(attr)!='string')?attr.join(','):attr}}
                                        </li>
                                        <li>ÁÆÄ‰ªã: {{popoverItem.summary}}</li>
                                    </ul>
                                </b-popover>
                            </b-card>
                        </b-card-group>
                    </b-tab>
                    <b-tab title="ÁúãËøáÁöÑ‰π¶">
                        <b-card-group columns>
                            <b-card :title="item.title" v-for="(item,index) in books" :key="index"
                                    bg-variant="dark" text-variant="white" :id="`book-card-${index}`">
                                <b-card-text>
                                    <ul>
                                        <li>ËØÑÂàÜÔºö{{(new Array(item.star + 1)).join('‚≠ê')}}</li>
                                        <li>Ê†áËÆ∞Êó∂Èó¥Ôºö{{formatDate(new Date(item.date), 'yyyy-MM-dd')}}</li>
                                        <li>Áü≠ËØÑÔºö{{item.comment}}</li>
                                    </ul>
                                </b-card-text>
                                <b-popover :target="`book-card-${index}`" triggers="click" @show="getDetail(item)"
                                           @hide="popImageUrl=''">
                                    <template v-slot:title>{{popoverItem.title}}</template>
                                    <b-img thumbnail
                                           v-bind:src="popImageUrl"
                                           center/>
                                    <br/>
                                    <ul>
                                        <li>ËØÑÂàÜ: {{popoverItem.rating.average}}</li>
                                        <li>‰ΩúËÄÖ: {{popoverItem.author}}</li>
                                        <li>isbn: {{popoverItem.isbn10+' | '+popoverItem.isbn13}}</li>
                                        <li>Âá∫ÁâàÁ§æ: {{popoverItem.publisher}}</li>
                                    </ul>
                                </b-popover>
                            </b-card>
                        </b-card-group>
                    </b-tab>
                    <b-tab title="Âê¨ËøáÁöÑÈü≥‰πê">
                        <b-card-group columns>
                            <b-card :title="item.title" v-for="(item,index) in musics" :key="index"
                                    bg-variant="dark" text-variant="white" :id="`music-card-${index}`">
                                <b-card-text>
                                    <ul>
                                        <li>ËØÑÂàÜÔºö{{(new Array(item.star + 1)).join('‚≠ê')}}</li>
                                        <li>Ê†áËÆ∞Êó∂Èó¥Ôºö{{formatDate(new Date(item.date), 'yyyy-MM-dd')}}</li>
                                        <li>Áü≠ËØÑÔºö{{item.comment}}</li>
                                    </ul>
                                </b-card-text>
                                <b-popover :target="`music-card-${index}`" triggers="click" @show="getDetail(item)"
                                           @hidden="popImageUrl=''">
                                    <template v-slot:title>{{item.title}}</template>
                                    <b-img thumbnail
                                           v-bind:src="popImageUrl"
                                           center/>
                                    <br/>
                                    <ul>
                                        <li v-for="(attr,name) in popoverItem.attrs" :key="name">
                                            {{name}}: {{(typeof(attr)!='string')?attr.join(','):attr}}
                                        </li>
                                        <li>ÁÆÄ‰ªã: {{popoverItem.summary}}</li>
                                    </ul>
                                </b-popover>
                            </b-card>
                        </b-card-group>
                    </b-tab>
                </b-tabs>
            </b-card>
        </b-col>
        <b-col md="auto" class="profile">
            <b-card no-body style="max-width: 20rem;" bg-variant="dark" text-variant="white">
                <template v-slot:header>
                    <h4 class="mb-0" align="center">‰∏™‰∫∫ËµÑÊñô</h4>
                </template>

                <b-card-body>
                    <b-card-img alt="Head" :src="user.userface" top=true class="userHead"></b-card-img>
                    <b-card-title :title="user.name" align="center"></b-card-title>
                    <b-card-sub-title class="mb-2">üíñ<!--{{user.heart}}--></b-card-sub-title>
                    <b-card-text>
                        {{user.desc}}
                    </b-card-text>
                </b-card-body>

                <b-list-group flush>
                    <b-list-group-item variant="dark">has watched {{count.f}} movies.</b-list-group-item>
                    <b-list-group-item variant="dark">has read {{count.b}} books.</b-list-group-item>
                    <b-list-group-item variant="dark">has heard {{count.m}} albums.</b-list-group-item>
                </b-list-group>

                <b-card-body>
                    <b-button variant="info" class="ml-2">follow</b-button>
                    <b-button variant="success" class="ml-2">message</b-button>
                    <b-button variant="danger" class="ml-2">block</b-button>
                </b-card-body>

                <b-card-footer>
                    <p>ÊùÉÈôêÔºö</p>
                    <ul>
                        <li v-for="(role,index) in user.roles" :key="index">{{role.name}}</li>
                    </ul>
                </b-card-footer>
            </b-card>
        </b-col>
    </b-row>
</template>

<script>
    export default {
        name: "UserHome",
        data() {
            return {
                user: {
                    roles: [],
                },
                Finished: [],
                movies: [],
                books: [],
                musics: [],
                count: {},
                popoverItem: {
                    alt_title: '',
                    image: '',
                    summary: '',
                    rating: {
                        average: '',
                    },
                    author: '',
                    isbn10: '',
                    isbn13: '',
                    publisher: '',
                    attrs: {
                        singer: [],
                        pubdate: [],
                        tracks: [],
                    }
                },
                popImageUrl: '',
            }
        },
        mounted() {
            this.user = this.$store.state.currentUser;
            this.getFinished();
            this.userItemsCount();
        },
        methods: {
            getFinished() {
                let url = '/finished/basic/' + this.user.id;
                this.getRequest(url).then(resp => {
                    if (resp) {
                        this.Finished = resp;
                        // console.log(this.Finished)
                        this.classify();
                        // console.log(this.movies)
                        // Trick to reset/clear native browser form validation state
                        this.show = false;
                        this.$nextTick(() => {
                            this.show = true
                        })
                    }
                })
            },
            classify() {
                for (let i = 0; i < this.Finished.length; i++) {
                    switch (this.Finished[i].eventType) {
                        case "f":
                            this.movies.push(this.Finished[i]);
                            break;
                        case "b":
                            this.books.push(this.Finished[i]);
                            break;
                        case "m":
                            this.musics.push(this.Finished[i]);
                            break;
                    }
                }
            },
            userItemsCount() {
                let url = '/finished/basic/count/' + this.user.id;
                this.getRequest(url).then(resp => {
                    if (resp) {
                        // console.log(resp);
                        this.count = resp;
                    }
                })
            },
            getDetail(item) {
                let id = item.eventId;
                let url = "/dapi/v2";
                switch (item.eventType) {
                    case "f":
                        url = url + "/movie/" + id;
                        break;
                    case "b":
                        url = url + "/book/" + id;
                        break;
                    case "m":
                        url = url + "/music/" + id;
                        break;
                }
                this.getRequest(url).then(resp => {
                    if (resp) {
                        this.popoverItem = resp;
                        console.log(this.popoverItem);
                        this.popImageUrl = this.imageUrlCut(resp.image);
                    }
                })
            },
            imageUrlCut(url) {
                return url.replace(/https:\/\/img\d.doubanio.com/i, "/dpic");
            },
            resetPopOver(){
                this.popImageUrl = '';
                this.popoverItem = {
                    alt_title: '',
                    image: '',
                    author: '',
                    summary: '',
                    rating: {
                        average: '',
                    },
                    isbn10: '',
                    isbn13: '',
                    publisher: '',
                    attrs: {
                        singer: [],
                        pubdate: [],
                        tracks: [],
                    }
                };
            }
        }
    }
</script>

